//! Color 
$primary-color:#fff;
$secondary-color:#000;
$border-color:#BABBC5;
$gray-color:#6C7279;
$active-color:#E71414;
$copyright-color:#808080;

$header-background-color:#252B49;
$button1-background-color:#FF4343;
$button1-hover-color: $active-color;
$our-work-background: rgba(255, 67, 67, 0.8);
$reviews-background:#5C3249;
$top-footer-background:#353C65;
$bottom-footer-background:#2C3154;

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

ul,
ol {
    margin: 0;
    padding: 0;
    list-style: none;
}

a {
    text-decoration: none;
}

body {
    font-family: Raleway, Arial, sans-serif;
    display: flex;
    flex-flow: column wrap;
    font-size: 18px;
}
@font-face {
    font-family: Raleway;
    src: url(../font/Raleway/Raleway-Regular.woff2),
    url(../font/Raleway/Raleway-Regular.ttf);
}
@font-face {
    font-family: Cardo;
    src: url(../font/Cardo/Cardo-Regular.woff2),
    url(../font/Cardo/Cardo-Regular.ttf);
}
@font-face {
    font-family: Cardo-italic;
    src: url(../font/Cardo/Cardo-Italic.woff2),
    url(../font/Cardo/Cardo-Italic.ttf);
}
.button {
    text-transform: uppercase;
    cursor: pointer;
    color: $primary-color;
    font-size: 24px;
    font-family: Raleway, Arial, serif;
    font-weight: 600;
    border: none;
}
.button:hover,
.button:focus {
    background-color: $button1-hover-color;
    transition: background-color ease-in .2s;
}
.button--red {
    background-color: $button1-background-color;
}

.header {
    min-height: 747px;
    background-image: url(../img/banner-background.jpg);
    background-color: $header-background-color;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
}

.header-nav {
    max-width: 1170px;
    margin: 0 auto;
    margin-bottom: 130px;

    &__list {
        display: flex;
        flex-flow: row wrap;
        align-items: center;
        border-bottom: 2px solid $border-color;
    }

    &__item {
        display: flex;
        flex-flow: row wrap;
        align-items: center;
        min-height: 125px;
    }

    &__item:not(:last-child) {
        margin-right: 15px;
    }

    &__link {
        font-size: 13px;
        text-transform: uppercase;
        padding: 10px 20px;
        color: $primary-color;
    }
}

.header-nav__link:hover,
.header-nav__link:hover {
    pointer-events: auto;
    transition: border ease-in-out .2s;
    border-bottom: 3px solid $border-color;
}

.headaer-nav__item--active {
    position: relative;
}

.header-nav__link--active {
    cursor: default;
    color: $active-color;
}

.header-nav__link--active::after {
    position: absolute;
    width: 100%;
    content: "";
    bottom: 0px;
    left: 0px;
    border-bottom: 3px solid $active-color;
}

.banner {
    width: 1170px;
    margin: 0 auto;
    color: $primary-color;
    text-align: center;
}

.banner__title {
    font-size: 64px;
    margin-bottom: 30px;
    font-weight: 600;
    text-transform: uppercase;
}

.banner__text {
    font-size: 24px;
    font-weight: 400;
    margin-bottom: 80px;
}

.banner__button {
    padding: 20px 90px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.250);
    margin-bottom: 120px;
}

.banner__button--active {
    box-shadow: 4px 4px 30px $button1-background-color;
}

.main {
    flex-grow: 2;
}

.feature {
    padding: 110px 170px;
}

.feature__list {
    width: 1170px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
    row-gap: 90px;
}

.feature__item {
    max-width: 370px;
    display: flex;
    flex-flow: column wrap;
    align-items: center;
}

.feature__img {
    margin-bottom: 30px;
}

.feature__title {
    padding: 15px 20px;
    margin-bottom: 20px;
    text-transform: uppercase;
    font-size: 24px;
    font-weight: 800;
}

.feature__text {
    color: $gray-color;
    font-size: 18px;
    text-align: center;
}

.our-work {
    margin-bottom: 115px;
    &__list {
        display: flex;
        flex-flow: row wrap;
    }
    &__item {
        width: 25%;
        position: relative;
    }
}
.our-work-link__content {
    display: none;
}
.our-work-link:hover .our-work-link__content,
.our-work-link:focus .our-work-link__content {
    display: block;
    background-color: $our-work-background;
    width: 377px;
    height: 339px;
    transition: display linear .2s;
}
.our-work-link__content {
    text-align: center;
    padding: 110px 0;
    top: 1px;
    left: 0px;
    z-index: 10;
    position: absolute;
    content:"";
    color: $primary-color;
}
.our-work-title {
    text-transform: uppercase;
    margin-bottom: 30px;
    font-size: 24px;
}
.our-work-text {
    margin-bottom: 15px;
    font-size: 18px;
}
.our-team {
    padding: 0 170px;
    margin-bottom: 115px;
&__list {
    display: flex;
    flex-flow: row wrap;
}
&__item {
    width: 270px;
    margin-right: 30px;
    margin-bottom: 60px;
}
&__item:nth-child(4n) {
    margin-right: 0px;
}
}
.our-team-img {
    margin-bottom: 30px;
}
.our-team-title {
    font-size: 24px;
    font-weight: 700;
    margin-bottom: 10px;
}
.our-team-position {
    display: inline-block;
    text-transform: uppercase;
    font-weight: 400;
    font-size: 18px;
    color: $button1-background-color;
    margin-bottom: 20px;
}
.our-team-text {
    color: $gray-color;
    margin-bottom: 35px;
}
.social-team__list {
    display: flex;
    flex-flow: row wrap;
}
.social-team__item:not(:last-child) {
    margin-right: 10px;
}
.reviews {
    margin-bottom: 115px;
}
.reviews-carousel {
    margin-bottom: 30px;
}
.reviews-content {
    display: flex;
    flex-flow: row wrap;
}
.reviews-content img {
    width: 50%;
}
.reviews-content--text {
    width: 50%;
    overflow: hidden;
    padding-left: 120px;
    padding-top: 115px;
    padding-right: 170px;
    background-color: $reviews-background;
    color: $primary-color;
}
.reviews-title {
    font-size: 36px;
    font-family: Cardo-italic, Arial, sans-serif;
    font-weight: 400;
    // font-style: italic;
}
.reviews-text {
    align-items: center;
    margin: 0 auto;
    font-family: 13px;
    text-transform: uppercase;
}
.reviews-page {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
&__item:not(:last-child) {
    margin-right: 25px;
}
&__item button {
    border: 1px solid $reviews-background;
    border-radius: 50%;
    width: 15px;
    height: 15px;
    background: transparent;
}
&__item--active button {
    background: $reviews-background;
    cursor: default;
}
}
.download {
    width: 1170px;
    margin: 0 auto;
    text-align: center;
    margin-bottom: 115px;
}
.download-title {
    font-size: 30px;
    margin-bottom: 15px;
}
.download-text {
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 45px;
}
.download-button {
    padding: 20px 70px;
}
.footer {
    flex-grow: 2;
    display: flex;
    flex-flow: column wrap;
    justify-content: end;
    height: auto;
    font-family: 'Raleway', 'Arial', serif;
    font-weight: 400;
}
.top-footer {
    padding: 0 170px;
    background-color: $top-footer-background; 
    padding-top: 70px;
    color: $primary-color;
    display: flex;
    flex-flow: row wrap;
    text-align: center;
    height: auto;
}
.footer-location,
.footer-social,
.footer-aboutus {
    width: 33%;
}
.footer-social-title {
    font-family: Raleway, Arial, serif;
    text-transform: uppercase;
    margin-bottom: 30px;
}

.footer-social-list {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
}
.footer-social__item:not(:last-child) {
    margin-right: 25px;
}
.bottom-footer {
    padding: 22px 170px;
    min-height: 90px;
    background-color: $bottom-footer-background;
    text-align: center;
    height: auto;
}
.footer-copyright-text {
    width: 370px;
    display: inline-block;
    text-align: center;
    color:$copyright-color;
    & b{
        color: $primary-color;
    }
}
@media screen and (max-width: 1024px){
    body {
        margin: 0 auto;
    }
    .header {
        margin: 0 auto;
        width: 1024px;
    }
    .header-nav__list {
        margin: 0 20px;
    }
    .banner {
        width: 984px;
    }
    .banner__title {
        font-size: 48px;
        font-weight: 700;
    }
    .banner__text {
        font-size: 18px;
    }
    .main {
        width: 1024px;
    }
    .feature {
        width: 1024px;
        padding: 70px 20px;
        margin: 0 auto;
    }
    .feature__list {
        width: 100%;
        column-gap: 20px;
    }
    .our-work__list {
        overflow: hidden;
    }
    .our-work-link:hover .our-work-link__content,
    .our-work-link:focus .our-work-link__content {
        width: 256px;
    }
    .our-team {
        padding: 0 20px;
    }
    .our-team__item {
        max-width: 227px;
    }

}
